import { create } from 'zustand';
import { OrganizationWithStats } from '../../types/credit';
import ajax from './ajax';

interface SuperAdminState {
    organizations: OrganizationWithStats[];
    selectedOrganization: OrganizationWithStats | null;
    dashboardStats: {
        totalOrganizations: number;
        activeOrganizations: number;
        totalCampaigns: number;
        activeCampaigns: number;
        totalMessagesSent: number;
        totalCredits: number;
        totalLockedCredits: number;
    } | null;
    isLoading: boolean;
    error: string | null;
    total: number;
}

interface SuperAdminActions {
    loadOrganizations: (params?: { limit?: number; offset?: number; search?: string }) => Promise<void>;
    loadOrganization: (id: string) => Promise<void>;
    addCredits: (orgId: string, amount: number, expiresAt?: Date, description?: string) => Promise<void>;
    toggleOrganizationStatus: (orgId: string, isActive: boolean) => Promise<void>;
    loadDashboardStats: () => Promise<void>;
    changePassword: (currentPassword: string, newPassword: string) => Promise<void>;
    clearError: () => void;
}

type SuperAdminStore = SuperAdminState & SuperAdminActions;

export const useSuperAdminStore = create<SuperAdminStore>((set) => ({
    organizations: [],
    selectedOrganization: null,
    dashboardStats: null,
    isLoading: false,
    error: null,
    total: 0,

    loadOrganizations: async (params) => {
        set({ isLoading: true, error: null });
        const queryParams = new URLSearchParams();
        if (params?.limit) queryParams.append('limit', params.limit.toString());
        if (params?.offset) queryParams.append('offset', params.offset.toString());
        if (params?.search) queryParams.append('search', params.search);

        const response = await ajax.get<{ organizations: OrganizationWithStats[]; total: number }>(
            `/api/super-admin/organizations?${queryParams.toString()}`
        );
        set({ organizations: response.organizations, total: response.total, isLoading: false });
    },

    loadOrganization: async (id) => {
        set({ isLoading: true, error: null });
        const response = await ajax.get<OrganizationWithStats>(`/api/super-admin/organizations/${id}`);
        set({ selectedOrganization: response, isLoading: false });
    },

    addCredits: async (orgId, amount, expiresAt, description) => {
        set({ isLoading: true, error: null });
        await ajax.post(`/api/super-admin/organizations/${orgId}/credits`, {
            amount,
            expiresAt,
            description,
        });
        const response = await ajax.get<{ organizations: OrganizationWithStats[]; total: number }>(
            '/api/super-admin/organizations'
        );
        set({ organizations: response.organizations, total: response.total, isLoading: false });
    },

    toggleOrganizationStatus: async (orgId, isActive) => {
        set({ isLoading: true, error: null });
        await ajax.patch(`/api/super-admin/organizations/${orgId}/status`, { isActive });
        set((state) => ({
            organizations: state.organizations.map((org) =>
                org.id === orgId ? { ...org, isActive } : org
            ),
            isLoading: false,
        }));
    },

    loadDashboardStats: async () => {
        set({ isLoading: true, error: null });
        const response = await ajax.get('/api/super-admin/dashboard-stats');
        set({ dashboardStats: response, isLoading: false });
    },

    changePassword: async (currentPassword, newPassword) => {
        set({ isLoading: true, error: null });
        await ajax.post('/api/super-admin/change-password', { currentPassword, newPassword });
        set({ isLoading: false });
    },

    clearError: () => set({ error: null }),
}));
