import React, { useEffect, useState } from 'react';
import { Button, Modal, TextInput } from '../../components';
import { useSuperAdminStore } from '../../services/super-admin';

const formatNumber = (num: number) => {
    if (num >= 1000000) return `${(num / 1000000).toFixed(1)}M`;
    if (num >= 1000) return `${(num / 1000).toFixed(1)}K`;
    return num.toLocaleString();
};

const formatDate = (date: Date | string) => {
    return new Date(date).toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
    });
};

export const Organizations: React.FC = () => {
    const {
        organizations,
        dashboardStats,
        isLoading,
        total,
        loadOrganizations,
        loadDashboardStats,
        addCredits,
        toggleOrganizationStatus,
    } = useSuperAdminStore();

    const [searchQuery, setSearchQuery] = useState('');
    const [showAddCreditsModal, setShowAddCreditsModal] = useState(false);
    const [selectedOrgId, setSelectedOrgId] = useState<string | null>(null);
    const [creditAmount, setCreditAmount] = useState('');
    const [creditExpiry, setCreditExpiry] = useState('');
    const [creditDescription, setCreditDescription] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    useEffect(() => {
        loadOrganizations();
        loadDashboardStats();
    }, []);

    const handleSearch = () => {
        loadOrganizations({ search: searchQuery });
    };

    const handleAddCredits = async () => {
        if (!selectedOrgId || !creditAmount) return;
        setIsSubmitting(true);
        await addCredits(
            selectedOrgId,
            parseInt(creditAmount),
            creditExpiry ? new Date(creditExpiry) : undefined,
            creditDescription || undefined
        );
        setIsSubmitting(false);
        setShowAddCreditsModal(false);
        setCreditAmount('');
        setCreditExpiry('');
        setCreditDescription('');
        setSelectedOrgId(null);
    };

    const openAddCreditsModal = (orgId: string) => {
        setSelectedOrgId(orgId);
        setShowAddCreditsModal(true);
    };

    const selectedOrg = organizations.find((o) => o.id === selectedOrgId);

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Organizations</h1>
                    <p className="text-gray-600 mt-1">Manage organizations and credits</p>
                </div>
            </div>

            {dashboardStats && (
                <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-7 gap-4">
                    <div className="bg-white rounded-lg border border-gray-200 p-4">
                        <p className="text-xs text-gray-500 uppercase tracking-wide">Organizations</p>
                        <p className="text-2xl font-bold text-gray-900 mt-1">{dashboardStats.totalOrganizations}</p>
                        <p className="text-xs text-green-600 mt-1">{dashboardStats.activeOrganizations} active</p>
                    </div>
                    <div className="bg-white rounded-lg border border-gray-200 p-4">
                        <p className="text-xs text-gray-500 uppercase tracking-wide">Total Credits</p>
                        <p className="text-2xl font-bold text-gray-900 mt-1">{formatNumber(dashboardStats.totalCredits)}</p>
                        <p className="text-xs text-orange-600 mt-1">{formatNumber(dashboardStats.totalLockedCredits)} locked</p>
                    </div>
                    <div className="bg-white rounded-lg border border-gray-200 p-4">
                        <p className="text-xs text-gray-500 uppercase tracking-wide">Campaigns</p>
                        <p className="text-2xl font-bold text-gray-900 mt-1">{formatNumber(dashboardStats.totalCampaigns)}</p>
                        <p className="text-xs text-blue-600 mt-1">{dashboardStats.activeCampaigns} active</p>
                    </div>
                    <div className="bg-white rounded-lg border border-gray-200 p-4">
                        <p className="text-xs text-gray-500 uppercase tracking-wide">Messages Sent</p>
                        <p className="text-2xl font-bold text-gray-900 mt-1">{formatNumber(dashboardStats.totalMessagesSent)}</p>
                    </div>
                </div>
            )}

            <div className="bg-white rounded-lg border border-gray-200">
                <div className="p-4 border-b border-gray-200">
                    <div className="flex flex-col sm:flex-row gap-3">
                        <div className="flex-1">
                            <TextInput
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.value)}
                                placeholder="Search organizations..."
                                onKeyDown={(e) => e.key === 'Enter' && handleSearch()}
                            />
                        </div>
                        <Button onClick={handleSearch} disabled={isLoading}>
                            Search
                        </Button>
                    </div>
                </div>

                <div className="overflow-x-auto">
                    <table className="w-full">
                        <thead className="bg-gray-50 border-b border-gray-200">
                            <tr>
                                <th className="text-left px-4 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider">Organization</th>
                                <th className="text-left px-4 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                                <th className="text-right px-4 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider">Credits</th>
                                <th className="text-right px-4 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider">Locked</th>
                                <th className="text-right px-4 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider">Available</th>
                                <th className="text-right px-4 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider">Campaigns</th>
                                <th className="text-right px-4 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider">Messages</th>
                                <th className="text-left px-4 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider">Created</th>
                                <th className="text-right px-4 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-200">
                            {isLoading ? (
                                <tr>
                                    <td colSpan={9} className="px-4 py-8 text-center text-gray-500">
                                        Loading...
                                    </td>
                                </tr>
                            ) : organizations.length === 0 ? (
                                <tr>
                                    <td colSpan={9} className="px-4 py-8 text-center text-gray-500">
                                        No organizations found
                                    </td>
                                </tr>
                            ) : (
                                organizations.map((org) => (
                                    <tr key={org.id} className="hover:bg-gray-50">
                                        <td className="px-4 py-3">
                                            <div>
                                                <p className="font-medium text-gray-900">{org.name}</p>
                                                {org.website && (
                                                    <p className="text-xs text-gray-500">{org.website}</p>
                                                )}
                                            </div>
                                        </td>
                                        <td className="px-4 py-3">
                                            <button
                                                onClick={() => toggleOrganizationStatus(org.id, !org.isActive)}
                                                className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                                                    org.isActive
                                                        ? 'bg-green-100 text-green-800 hover:bg-green-200'
                                                        : 'bg-red-100 text-red-800 hover:bg-red-200'
                                                }`}
                                            >
                                                {org.isActive ? 'Active' : 'Inactive'}
                                            </button>
                                        </td>
                                        <td className="px-4 py-3 text-right font-medium text-gray-900">
                                            {formatNumber(org.creditBalance)}
                                        </td>
                                        <td className="px-4 py-3 text-right text-orange-600">
                                            {formatNumber(org.lockedCredits)}
                                        </td>
                                        <td className="px-4 py-3 text-right text-green-600 font-medium">
                                            {formatNumber(org.availableCredits)}
                                        </td>
                                        <td className="px-4 py-3 text-right text-gray-600">
                                            {org.totalCampaigns}
                                            {org.activeCampaigns > 0 && (
                                                <span className="text-blue-600 ml-1">({org.activeCampaigns})</span>
                                            )}
                                        </td>
                                        <td className="px-4 py-3 text-right text-gray-600">
                                            {formatNumber(org.totalMessagesSent)}
                                        </td>
                                        <td className="px-4 py-3 text-sm text-gray-500">
                                            {formatDate(org.createdAt)}
                                        </td>
                                        <td className="px-4 py-3 text-right">
                                            <Button
                                                size="sm"
                                                onClick={() => openAddCreditsModal(org.id)}
                                            >
                                                Add Credits
                                            </Button>
                                        </td>
                                    </tr>
                                ))
                            )}
                        </tbody>
                    </table>
                </div>

                {total > organizations.length && (
                    <div className="p-4 border-t border-gray-200 text-center">
                        <p className="text-sm text-gray-500">
                            Showing {organizations.length} of {total} organizations
                        </p>
                    </div>
                )}
            </div>

            <Modal
                isOpen={showAddCreditsModal}
                onClose={() => setShowAddCreditsModal(false)}
                title="Add Credits"
            >
                <div className="space-y-4">
                    {selectedOrg && (
                        <div className="bg-gray-50 rounded-lg p-4">
                            <p className="font-medium text-gray-900">{selectedOrg.name}</p>
                            <p className="text-sm text-gray-500 mt-1">
                                Current balance: <span className="font-medium">{formatNumber(selectedOrg.creditBalance)}</span>
                                {' | '}
                                Available: <span className="font-medium text-green-600">{formatNumber(selectedOrg.availableCredits)}</span>
                            </p>
                        </div>
                    )}

                    <TextInput
                        label="Credit Amount"
                        type="number"
                        value={creditAmount}
                        onChange={(e) => setCreditAmount(e.value)}
                        placeholder="Enter amount"
                        required
                    />

                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                            Expiry Date (Optional)
                        </label>
                        <input
                            type="date"
                            value={creditExpiry}
                            onChange={(e) => setCreditExpiry(e.target.value)}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                            min={new Date().toISOString().split('T')[0]}
                        />
                        <p className="text-xs text-gray-500 mt-1">
                            Leave empty for credits that never expire
                        </p>
                    </div>

                    <TextInput
                        label="Description (Optional)"
                        value={creditDescription}
                        onChange={(e) => setCreditDescription(e.value)}
                        placeholder="e.g., Monthly allocation, Promotional credits"
                    />

                    <div className="flex justify-end space-x-3 pt-4">
                        <Button
                            variant="default"
                            onClick={() => setShowAddCreditsModal(false)}
                            disabled={isSubmitting}
                        >
                            Cancel
                        </Button>
                        <Button
                            onClick={handleAddCredits}
                            disabled={!creditAmount || parseInt(creditAmount) <= 0 || isSubmitting}
                        >
                            {isSubmitting ? 'Adding...' : 'Add Credits'}
                        </Button>
                    </div>
                </div>
            </Modal>
        </div>
    );
};

export default Organizations;
